.o4liXq_pluginWrap {
  height: 41px;
  align-items: center;
  margin: 0 1rem;
  display: flex;
  font-size: .8rem !important;
}

.TcUz5W_toggleButton {
  background-color: var(--grayLight);
  border: 1px solid var(--grayMedium);
  color: var(--textSubtleLight);
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  border-radius: 6px;
  align-items: center;
  gap: .5em;
  padding: 2px .3rem 2px .5rem;
  line-height: 1.4em;
  transition: all .1s ease-out;
  display: flex;
}

.TcUz5W_toggleButton:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.TcUz5W_toggleButton:not(:disabled):hover {
  background-color: var(--grayLighter);
  color: var(--textOnWhite);
}

.TcUz5W_buttonIcon {
  background-color: var(--grayMedium);
  color: var(--textOnGray);
  border-radius: 3px;
  padding: 1px 4px;
  font-size: 80%;
  transition: background-color .1s ease-out;
}

.TcUz5W_toggleButton:not(:disabled):hover .TcUz5W_buttonIcon {
  background-color: var(--primaryBlue);
}

